.info_wrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
  padding: calc(var(--index) * 2);
  background-color: var(--bg-color);
  border-radius: calc(var(--index) * 0.75);
  margin-bottom: calc(var(--index) * 1);
  color: var(--black);
  font-weight: 600;
  box-shadow: 0 0 10px var(--bg-green);
}
.info_wrapper h2 {
  font-size: calc(var(--index) * 1.2);
  color: var(--dark-grey);
  font-weight: 700;
  margin-bottom: calc(var(--index) * 1.2);
}
