.footer_mobile {
  display: none;
}
@media (max-width: 767px) {
  .footer_mobile {
    display: flex;
    justify-content: space-between;
  }
}
.item_container {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #fff;
  font-size: 8px;
}
.icon {
  fill: #fff;
  font-size: 30px;
}
.icon_menu {
  fill: #fff;
  font-size: 40px;
}
